@charset "utf-8";
*{margin:0px;padding:0px;}
ul, li, ol{list-style:none;margin:0px;padding:0px;}
body{
  font-family:"맑은 고딕", arial, sans-serif;
  font-size:12px;
  color:#333;
}
a{text-decoration:none;color:#333;}

/*헤더 서식*/
header{
  width:100%;
  height:156px;
  box-shadow:0px 0px 3px #333;
}
.h_inner{
  margin:0px auto;
  width:1050px;
  transform:translateY(50px);
}
.txt01, .txt02{
  position:absolute;
  top:10px;left:100px;
  font-size:17px;
  color:#ccc;
  font-weight:bold;
  cursor:pointer;
}
.txt01, .txt02:hover{
  color:#680F87;
}
.txt02{
  left:180px;
}
.bar{
  font-size:15px;
  position:absolute;
  top:11px;left:173px;
  color:#ccc;
}
i.fas{
  font-size:15px;
  color:#000;
}
.search{
  width:350px;height:40px;
  border:1px solid #680F87;
  border-radius:5px;
  transform:translate(350px,-50px);
  outline:#680F87;
  padding:2px 10px 2px 10px;
}
.gnb{
  display:flex;
  justify-content:space-around;
  transform:translateY(-15px);
}
.join{
  display:flex;
  justify-content:center;
  padding-right:10px;
  transform:translate(450px, -150px);
  font-weight:700;
}
.signup::after{
  content:'|';
  color:#ccc;
}
.login::after{
  content:'|';
  color:#ccc;
}
.signup{color:#680F87;}

/*메인 서식*/
.slide, .product{
  width:100%;
  padding:30px 0px;
  height:600px;
  margin:50px auto 50px auto;
  /* background:rgb(172, 241, 172); */
}
.slide h2{text-align:center;}
.m_product img{
  margin:0px auto;
}
.m_product{
  width:1050px;
  margin:0px auto;
}
.m_product ul{
  display:flex;
  width:1050px;
  justify-content:space-around;
}
.m_product ul li{
  width:260px;
  overflow:hidden;
}
.img1, .img2, .img3, .img4{
  width:250px;height:300px;
  /* border:1px solid #000; */
  background-size:100%;
  /* margin-right:12.5px; */
  overflow:hidden;
  transition:0.5s;
}
.img1:hover, .img2:hover, .img3:hover, .img4:hover{
  scale:1.05;
}
.m_product .img1{
  background-image:url('/public/images/today01.jpg');
}
.m_product .img2{
  background-image:url('/public/images/today02.jpg');
}
.m_product .img3{
  background-image:url('/public/images/today03.jpg');
}
.m_product .img4{
  background-image:url('/public/images/today04.jpg');
}
.deli{
  color:#777;
  font-size:14px;
  font-weight:600;
  display:block;
  margin-top:5px;
}
.menu{
  display:block;
  font-weight:600;
}
.price, .sale{
  font-size:15px;
  font-weight:bold;
}
.sale{
  color:#f00;
}
.des{
  color:#777;
  font-weight:600;
}

.p_list img{
  margin:0px auto;
}
.p_list{
  width:1050px;
  margin:0px auto;
}
.p_list ul{
  display:flex;
  width:1050px;
  justify-content:space-around;
}
.p_list ul li{
  /* width:350px; */
  overflow:hidden;
}
.p_list ul li p{
  position:absolute;
  z-index:100;
}
.p_list.img1, .p_list .img2, .p_list .img3, .p_list .img4{
  width:250px;height:300px;
  /* border:1px solid #000; */
  background-size:100%;
  /* margin-right:12.5px; */
  overflow:hidden;
  transition:0.5s;
}
.p_list .img1:hover, .p_list .img2:hover, .p_list .img3:hover, .p_list .img4:hover{
  scale:1.05;
}
.p_list .img1{
  background-image:url('/public/images/list01.jpg');
}
.p_list .img2{
  background-image:url('/public/images/list02.jpg');
}
.p_list .img3{
  background-image:url('/public/images/list03.jpg');
}
.p_list .img4{
  background-image:url('/public/images/list04.jpg');
}
.p_list h2{text-align:center;}
.p_list ul{
  display:flex;
  justify-content:center;
  /* background:#f00; */
}
.p_list ul li{
  width:250px;height:300px;
  /* margin-right:50px; */
}
.p_list ul li{
  width:260px;
  /* background-image:url(''); */
}



footer{
  width:100%;height:580px;
  border-top:1px solid #ccc;
  background:#f5f5dc;
}
.f_inner{
  width:1050px;
  margin:0px auto;
  height:580px;background:#ff0;
}

.f_txt01{
  font-size:20px;
  font-weight:700;
}
.f_txt02{
  font-size:25px;
  font-weight:700;
}
.f_txt03{
  font-size:14px;
  font-weight:550;
}

/*슬라이드 서식*/
.slide-wrapper{
  width:1920px;height:300px;
  margin:0px auto;
  border:3px solid #000;
  position:relative;
}
.slide{
  display:flex;
  width:1620px;
  position:absolute;
  left:0px;top:0px;
  transition:0.3s;
}
.slide > li{
  width:1920px;height:370px;
  background:#ff0;
}
 /*li태그 마지막 번째를 제외한 나머지에게 마진값 설정*/
.slide > li:not(:last-child){margin-right:30px;}
.slide > li > div{}
.btn_group{}
.btn_group > input[type=button]
{

}
.btn_group .prev, .btn_group .next{
  width:30px;height:30px;
  position:absolute;
  top:130px;
  z-index:100;
}
.btn_group .prev{}
.btn_group .next{
  right:0px;
}