form{
  text-align:center;
}
form legend{
  text-align:center;
  margin-bottom:20px;
  font-weight:bold;
  font-size:20px;
}
form p{
  margin-left:220px;
  font-size:12px;
  font-weight:550;
}
input, button{
  width:340px;height:44px;
  display:block;
  margin:0px auto 10px auto;
  padding: 0px 11px 1px 15px;
  border-radius:5px;
  font-size:15px;
}
input{
  border:solid 1px #919191;
  outline:1px #000;
}
button{
  width:370px;height:50px;
}
#login, .SignUp{
  font-weight:bold;
  border:solid 1px #5F0080;
}
#login{
  background:#5F0080;
  color:#fff;
}
.SignUp{
  color:#5F0080;
}